<template>
    <div class="Box" ref="Box" @mousemove="Mousemove" @mousedown="Mousedown">
        <div ref="outer" id="WorldMap" @mousewheel="Scroll" :style="Style"></div>
    </div>
</template>
<script>
    import anime from "animejs";
    let svg;

    export default {
        data() {
            return {
                diffX: 0,
                diffY: 0,
                moving: false,
                scale: 1,
                left: 0,
                top: 0,
            };
        },
        methods: {
            Mousedown(e) {
                this.moving = true;
                this.diffX = e.clientX;
                this.diffY = e.clientY;
            },
            Mousemove(e) {
                if (this.moving) {
                    let Left = e.clientX - this.diffX;
                    let Top = e.clientY - this.diffY;

                    this.diffX = e.clientX;
                    this.diffY = e.clientY;
                    let { left, top } = getComputedStyle(svg);

                    this.left = parseInt(left) + Left * this.scale;
                    this.top = parseInt(top) + Top * this.scale;
                }
                return false;
            },
            Mouseup(e) {
                this.moving = false;
            },
            Scroll(e) {
                const min = 0.5,
                    max = 10;
                if (this.scale >= min && this.scale <= max) {
                    if (e.wheelDelta > 0) {
                        this.scale += min;
                    } else {
                        this.scale -= min;
                    }
                } else {
                    // 边界越界控制
                    if (this.scale < min) {
                        this.scale = min;
                    } else if (this.scale > max) {
                        this.scale = max;
                    }
                }
            },
        },
        async mounted() {
            this.$refs.outer.innerHTML = await fetch("./worldMap.svg").then((res) => res.text());
            svg = this.$refs.outer;
            [...svg.querySelectorAll("path")].forEach((Element) =>
                Element.addEventListener("dblclick", (e) => {
                    const { x, y, width, height } = Element.getBoundingClientRect();
                    let { left, top } = getComputedStyle(svg);
                    let { width: Width, height: Height } = getComputedStyle(this.$refs.Box);
                    [left, top, Width, Height] = [left, top, Width, Height].map((i) => parseInt(i));
                    console.log(x, y);
                    anime({
                        targets: svg,
                        left: left + Width / 2 - (x + width / 2),
                        top: top + Height / 2 - (y + height / 2),
                        duration: 2000,
                    });
                })
            );
            window.addEventListener("mouseup", this.Mouseup);
            console.log(svg);
        },

        destroyed() {
            window.removeEventListener("mouseup", this.Mouseup);
        },
        computed: {
            Style() {
                return {
                    transform: `scale(${this.scale})`,
                    left: `${this.left}px`,
                    top: `${this.top}px`,
                };
            },
        },
        watch: {},
    };
</script>
<style scoped lang="less">
    .Box {
        height: 100%;
        width: 100%;
        position: relative;
        overflow: hidden;
        cursor: move;
        transform-origin: center center;
    }
</style>
<style>
    #WorldMap {
        width: 200vw;
        position: absolute;
        top: 0;
        left: 0;
    }
</style>
